html, body, section, article,
h1, h2, p, strong, em, a {
  margin: 0;
  border: 0;
  padding: 0;
  outline: 0;
  box-sizing: inherit;
  font: inherit;
  text-decoration: inherit;
  text-align: inherit;
  color: inherit;
  background: transparent;
}

li {
  list-style: none;
}

body {
  height: 100vh;
  // background-image: linear-gradient(#16c35b, #f9f35a);
  // background-image: url(../assets/baquette.png);
  background: url(../assets/baquette.png),
    repeating-linear-gradient(
    35deg,
    #16c35b,
    #16c35b 35px,
    #f9f35a 35px,
    #f9f35a 50px);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: cover;
  font-family: Arial, Helvetica, sans-serif;
}

.all-breads, .all-cheese, .all-meat, .single-sandwich {
  width: 35px;
  height: auto;
}

header {
  height: 100px;
}

.title-container {
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.title {
  display: flex;
}

.mine-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.mine {
  display: flex;
  width: 40%;
  justify-content: space-evenly;
  align-items: baseline;
}

.icon-container {
  display: flex;
  width: 16%;
  justify-content: space-between;
}

li > a > img {
  width: 40px;
  height: auto;
}

.canvas-and-options {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  top: 30px;
  justify-content: space-around;
  align-items: center;
  border-bottom: 2px solid rgba(204, 147, 73);
  border-top: 2px solid rgba(204, 147, 73);
  padding: 10px;
  background-color: rgba(250, 249, 247, .7);
}

.container-chart {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px;
  width: 500px;
  height: 270px;
  background-color: rgba(250, 249, 247, .5);
  //background-color: rgba(158, 161, 122, 0.5);
  border: 2px solid rgba(204, 147, 73);
  border-radius: 5%;
}

.total-cal-container {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 250px;
}

.total-cal-text {
  color: rgba(0, 0, 0, 0.6);
}

#total-cal-num {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid rgba(204, 147, 73);
  border-radius: 7%;
  color: rgba(0, 0, 0, 0.6);
  width: 50px;
  height: 25px;
}

.options {
  display: grid;
  grid-template-columns: 100px 100px 100px 100px 100px 100px;
  column-gap: 5px;
  row-gap: 5px;
}

.option:hover {
  background-color: rgb(39, 162, 84, .5);
  width: 100px;
  height: 40px;
  box-sizing: border-box;
}

li[clicked="yes"] {
  border: 2px solid rgb(30, 135, 69);
}

.option {
  display: flex;
  border: 1px solid rgb(39, 162, 84);
  width: 100px;
  height: 40px;
  box-sizing: border-box;
  border-radius: 7%;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  font-size: 10px;
  background-color: #39f082;
  cursor: pointer;
}

.clear-button, #selected-list {
  display: flex;
  border: 1px solid rgb(164, 104, 25);
  background-color: rgb(215, 149, 63);
  width: 100px;
  height: 40px;
  box-sizing: border-box;
  border-radius: 7%;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.2s all;
}

.clear-button:active, #selected-list:active {
  transform: scale(0.92);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

#selected-list #selected-list-text {
  visibility: hidden;
  width: 300px;
  background-color: #f9f35a;
  color: #16c35b;
  border-radius: 5px;

  position: absolute;
  z-index: 1;
}

#selected-list.active #selected-list-text {
  visibility: visible;
}